$calendar-width: 726px;
$cell-and-gap: 101px;

$margin-monday: 0;
$margin-tuesday: $cell-and-gap;
$margin-wednesday: $cell-and-gap * 2;
$margin-thursday: $cell-and-gap * 3;
$margin-friday: $cell-and-gap * 4;
$margin-saturday: $cell-and-gap * 5;
$margin-sunday: $cell-and-gap * 6;

$begining-margin:
  'mon' $margin-monday,
  'tue' $margin-tuesday,
  'wed' $margin-wednesday,
  'thu' $margin-thursday,
  'fri' $margin-friday,
  'sat' $margin-saturday,
  'sun' $margin-sunday;
  